import React, { useState, useEffect } from "react";
import { userContext } from "../../services/userContext.js";
import { Redirect, Link } from "react-router-dom";
import UserProfilePicture from "../UserProfilePicture.jsx";

const Notifications = ({ user }) => {
  return (
    <>
      <div
        className="absolute bg-white rounded p-6 border z-10 right-0"
        id="notifications"
      >
        <h1 className="text-black">Meldingen</h1>
        {user.receivedNotifications &&
          user.receivedNotifications.map((notification) => {
            return (
              <Notification key={notification.id} notification={notification} />
            );
          })}
      </div>
    </>
  );
};

export default Notifications;

const Notification = ({ notification }) => {
  const { giver, photo } = notification;

  console.log(notification);

  const generateText = () => {
    if (notification.action === "like") {
      return `${
        !giver.firstame ? giver.username : giver.firstname
      } vind je foto ${photo.title} leuk`;
    }
    if (notification.action === "comment") {
      return `${
        !giver.firstame ? giver.username : giver.firstname
      } heeft gereageerd op je foto ${photo.title}`;
    }
    if (notification.action === "follow") {
      return `${
        !giver.firstame ? giver.username : giver.firstname
      } volgt je nu!`;
    }
  };

  const generateLink = () => {
    const className = "absolute top-0 left-0 w-full h-full z-10";
    if (notification.action === "like" || notification.action === "comment") {
      return <Link to={`/foto/${photo.slug}`} className={className} />;
    }
    if (notification.action === "follow") {
      return <Link to={`/fotograaf/${giver.slug}`} className={className} />;
    }
  };

  return (
    <>
      <div className="flex relative my-4">
        {generateLink()}
        <div className="mr-4">
          <UserProfilePicture profile={notification.giver} size={8} />
        </div>
        <div>{generateText()}</div>
      </div>
    </>
  );
};
